import './style.css';
const articleContent = require('./html/article.html');

import Article from './js/article.js';
import Story from './js/story.js';
import Photos from './js/photos.js';

function bindArticle() {
    let article = new Article();
    let photos = new Photos(
        article,
        document.querySelector('section.photos')
    );
    let story = new Story(
        article,
        document.querySelector('section.story'),
        document.querySelector('div.viewport')
    );
}

function meta(document) {
    const element = document.createElement('meta');
    element.name = 'viewport';
    element.content = 'width=device-width, initial-scale=1, maximum-scale=1, user-scalable=0';

    return element;
}

document.body.appendChild(meta(document));
document.body.insertAdjacentHTML('beforeend', articleContent);
bindArticle();