'use strict';

export default class Story {
    constructor(article, story, viewport) {
        this.article = article;
        this.storyEl = story;
        this.viewportEl = viewport;
        let that = this;
        viewport.onmousewheel = function(ev) {
            scrollViewport(ev.wheelDelta < 0, that.storyEl, that.viewportEl, that.state, ev.target.offsetLeft);
            return preventPropagation(ev);
        };
        viewport.onclick = function(ev) {
           if (window.getSelection().isCollapsed) {
               moveViewportToLeft(that.article, that.storyEl, that.viewportEl, that.state);
           }
           return preventPropagation(ev);
       };

        this.state = {
            left: 0
        }
        article.registerListener(this);
    }

    onRequestReadView(ev) {
        this.storyEl.classList.add('animateSlideToMenu');;
    }
}

const VIEWPORT_START_POSITION = 0;
const VIEWPORT_MOVE = 600;

function preventPropagation(ev) {
    ev.preventDefault();
    ev.stopPropagation();
    return false;
}

function moveViewportToLeft(article, storyEl, viewportEl, state, clickLeftPosition) {
    if (!article.isInReadView()) {
        article.dispatch({name: 'RequestReadView'})
        state.left = 0;
    } else {
        let currentPosition = Math.abs(state.left) + storyEl.scrollWidth;
        if (currentPosition < viewportEl.scrollWidth) {
            let cs = document.querySelector('.selected');
            var next = cs.nextElementSibling
            while (next != null && (next.tagName.toLowerCase() != 'p' || next.offsetLeft < clickLeftPosition)) {
                next = next.nextElementSibling;
            }
            if (next != null) {
                cs.classList.remove('selected');
                next.classList.add('selected');
            }
            state.left -= VIEWPORT_MOVE;
        } else {
            let cs = document.querySelector('.selected');
            let start = document.querySelector('.start');
            cs.classList.remove('selected');
            start.classList.add('selected');

            state.left = VIEWPORT_START_POSITION;
        }
        viewportEl.style.left = state.left + "px"
    }
}

function scrollViewport(leftDirection, storyEl, viewportEl, state) {
    var currentPosition = Math.abs(state.left) + storyEl.scrollWidth;
    var cs = document.querySelector('.selected');
    if (leftDirection) {
        var next = cs.nextElementSibling
        while (next != null && next.tagName.toLowerCase() != 'p') {
            next = next.nextElementSibling;
        }
        if (next != null) {
            cs.classList.remove('selected')
            next.classList.add('selected')
        }

        if (currentPosition < viewportEl.scrollWidth) {
            if (next.offsetLeft + VIEWPORT_MOVE + state.left > storyEl.scrollWidth) {
                state.left -= VIEWPORT_MOVE;
            }
        }
    } else {
        var prev = cs.previousElementSibling
        while (prev != null && prev.tagName.toLowerCase() != 'p') {
            prev = prev.previousElementSibling;
        }
        if (prev != null) {
            cs.classList.remove('selected')
            prev.classList.add('selected')
        }
        if (state.left < 0) {
            if (prev.offsetLeft - VIEWPORT_MOVE + state.left < VIEWPORT_START_POSITION) {
                state.left += VIEWPORT_MOVE;
            }
        }
    }
    viewportEl.style.left = state.left + "px"
}