export default class Photos {

    constructor(article, photosEl) {
        this.photosEl = photosEl;
        this.heroEl = photosEl.querySelector('img.hero');
        this.menuEl = photosEl.querySelector('.sideMenu');

        article.registerListener(this);
    }

    onRequestReadView(ev) {
        this.photosEl.classList.add('animateHideHero');
        this.heroEl.classList.add('animateHideHero');
        this.menuEl.classList.add('animateShowMenu');
    }
}